.hidden {
  display: none !important; }

.center-item {
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%); }

.App-GetInTouch {
  padding: 20px; }
  .App-GetInTouch-header {
    display: flex;
    flex-direction: column;
    margin-bottom: 50px; }
  .App-GetInTouch-form {
    display: flex; }
    .App-GetInTouch-form-controls {
      width: 100%; }
    .App-GetInTouch-form .App-ButtonIcon {
      display: block;
      float: right; }
    .App-GetInTouch-form .form-group {
      margin-bottom: 20px; }
      .App-GetInTouch-form .form-group .label {
        display: flex;
        justify-content: space-between; }
      .App-GetInTouch-form .form-group .form-control-input {
        resize: none;
        width: 600px;
        margin-left: 20px; }
        .App-GetInTouch-form .form-group .form-control-input.textarea {
          height: 200px; }
