.hidden {
  display: none !important; }

.center-item {
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%); }

.App-PortfolioCard {
  max-width: 400px;
  background-color: #fff;
  display: inline-block;
  margin: 20px;
  box-shadow: 0 0 5px #ccc; }
  .App-PortfolioCard:hover {
    box-shadow: 0 0 5px #999; }
    .App-PortfolioCard:hover .action-button {
      opacity: 1; }
  .App-PortfolioCard-image-container {
    position: relative; }
    .App-PortfolioCard-image-container:hover .button-overlay {
      opacity: 1; }
    .App-PortfolioCard-image-container:hover .image {
      opacity: 0.33; }
    .App-PortfolioCard-image-container .image {
      cursor: pointer;
      width: 400px;
      transition: 0.5s ease; }
    .App-PortfolioCard-image-container .button-overlay {
      opacity: 0;
      position: absolute;
      top: 50%;
      left: 50%;
      transform: translate(-50%, -50%); }
  .App-PortfolioCard .content {
    padding: 20px; }
  .App-PortfolioCard .action-button {
    margin-right: 20px;
    opacity: 0;
    transition: 0.5s ease; }
