.hidden {
  display: none !important; }

.center-item {
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%); }

.App-ButtonIcon, .App-ButtonIcon.primary, .App-ButtonIcon.danger {
  border-radius: 5px;
  border: 1px solid #597ef7;
  transition: 0.5s ease;
  line-height: 1.55;
  padding: 6px 12px; }
  .App-ButtonIcon:hover, .App-ButtonIcon.primary:hover, .App-ButtonIcon.danger:hover {
    background-color: #1d39c4;
    border-color: #1d39c4; }
    .App-ButtonIcon:hover .icon, .App-ButtonIcon.primary:hover .icon, .App-ButtonIcon.danger:hover .icon {
      color: #fff; }
    .App-ButtonIcon:hover .text, .App-ButtonIcon.primary:hover .text, .App-ButtonIcon.danger:hover .text {
      color: #fff; }
  .App-ButtonIcon .icon, .App-ButtonIcon.primary .icon, .App-ButtonIcon.danger .icon {
    color: #1d39c4;
    vertical-align: middle; }
  .App-ButtonIcon .text, .App-ButtonIcon.primary .text, .App-ButtonIcon.danger .text {
    margin-left: 5px;
    font-weight: bold;
    color: #1d39c4; }

.App-ButtonIcon.borderless {
  border: none;
  background: transparent; }
  .App-ButtonIcon.borderless-danger {
    border: none;
    background: transparent; }
    .App-ButtonIcon.borderless-danger .icon {
      color: #ff4d4f; }
      .App-ButtonIcon.borderless-danger .icon:hover {
        color: #cf1322; }
  .App-ButtonIcon.borderless .icon {
    color: #597ef7; }
    .App-ButtonIcon.borderless .icon:hover {
      color: #1d39c4; }

.App-ButtonIcon.primary, .App-ButtonIcon.danger {
  border: 1px solid #597ef7;
  background-color: #597ef7; }
  .App-ButtonIcon.primary .icon, .App-ButtonIcon.danger .icon {
    color: #fff; }
  .App-ButtonIcon.primary .text, .App-ButtonIcon.danger .text {
    color: #fff; }

.App-ButtonIcon.danger {
  border: 1px solid #ff4d4f;
  background-color: #ff4d4f; }
  .App-ButtonIcon.danger:hover {
    background-color: #cf1322;
    border-color: #cf1322; }
