.hidden {
  display: none !important; }

.center-item {
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%); }

.App-Modal-wrapper {
  z-index: 9999;
  position: fixed;
  top: 0;
  right: 0;
  left: 0;
  bottom: 0;
  background-color: rgba(0, 0, 0, 0.65);
  height: 100%; }

.App-Modal-content {
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  width: 400px;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  border: solid 1px #555555;
  background: #fff; }
  .App-Modal-content .header {
    height: 50px;
    line-height: 10px;
    margin-bottom: 20px;
    padding: 20px; }
    .App-Modal-content .header.primary {
      background-color: #597ef7; }
    .App-Modal-content .header.danger {
      background-color: #ff4d4f; }
    .App-Modal-content .header-message {
      color: #fff; }
  .App-Modal-content .button-group {
    display: flex;
    flex-direction: row;
    justify-content: space-between;
    padding: 20px; }
