.hidden {
  display: none !important; }

.center-item {
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%); }

.App-SideBar {
  padding: 20px;
  background-color: #000000;
  height: 100vh; }
  .App-SideBar h1 {
    color: #1d39c4; }
  .App-SideBar-router-group {
    display: flex;
    flex-direction: column;
    margin-left: 20px; }
    .App-SideBar-router-group .route {
      color: #555555;
      margin-bottom: 10px; }
      .App-SideBar-router-group .route::before {
        font-family: 'Font Awesome 5 Free';
        font-weight: 600;
        content: '\f054';
        margin: 0 5px 0 -15px; }
      .App-SideBar-router-group .route .route-link {
        color: #555555; }
      .App-SideBar-router-group .route:hover, .App-SideBar-router-group .route .route-link:hover {
        color: #597ef7; }
    .App-SideBar-router-group .active, .App-SideBar-router-group .active:hover {
      font-weight: bold;
      color: #1d39c4; }
