.hidden {
  display: none !important; }

.center-item {
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%); }

.App-MarkDownForm {
  display: flex;
  flex-direction: column;
  padding: 20px;
  background-color: #fff; }
  .App-MarkDownForm.small-size .text-area {
    height: 300px; }
  .App-MarkDownForm .header .markdownform-btn {
    margin-right: 20px; }
  .App-MarkDownForm .header .text-display {
    color: #cf1322;
    margin-left: 20px; }
  .App-MarkDownForm .header .header-input {
    width: 400px;
    margin-right: 10px; }
  .App-MarkDownForm .text-area {
    resize: none;
    max-width: 100%;
    height: 600px;
    margin: 20px 0px; }
  .App-MarkDownForm .button-group {
    display: flex;
    flex-direction: row;
    justify-content: space-between; }
    .App-MarkDownForm .button-group .staging-btn {
      margin-right: 10px; }
