.hidden {
  display: none !important; }

.center-item {
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%); }

body {
  font-family: "Roboto", Helvetica, Arial, sans-serif;
  line-height: 1.55;
  background-color: #f4f4f4; }

textarea:focus, input:focus {
  outline: none; }

a {
  text-decoration: none; }

p {
  color: #999999; }

input, textarea {
  height: 40px;
  border: solid 1px #555555;
  border-radius: 5px;
  padding: 0px 10px; }

textarea {
  padding: 10px; }

button, a {
  cursor: pointer;
  outline: 0; }
