.hidden {
  display: none !important; }

.center-item {
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%); }

.App-ExternalLinks {
  display: flex;
  margin-top: 20px; }
  .App-ExternalLinks .external-link {
    border: solid 1px #555555;
    text-align: center;
    color: #999999;
    margin-right: 20px; }
    .App-ExternalLinks .external-link.sidebar {
      border-radius: 50%;
      width: 40px;
      height: 40px;
      line-height: 40px; }
    .App-ExternalLinks .external-link.getintouch {
      border-radius: 50%;
      width: 70px;
      height: 70px;
      line-height: 85px; }
    .App-ExternalLinks .external-link:hover {
      color: #1d39c4;
      border-color: #1d39c4; }
